/*
*   Name: Bizagi Tablet Render Text Extension
*   Author: UI Forms Team
*   Comments:
*   -   This script will redefine the text render class to adjust to tablet devices
*/

// Extends from render tablet base
bizagi.rendering.tablet.render.extend("bizagi.rendering.tablet.text", bizagi.rendering.text.prototype);
// Extends itself
bizagi.rendering.tablet.text.extend("bizagi.rendering.tablet.text", {}, {
    
    //#region Side effects funtions

    /**
     * Template method to implement in each device to customize each render after processed         
     */
    postRender: function () {
        // Call base
        this._super();
        
        // Set the default maxlenght for the input
        if (this.properties.maxLength > 0) {
            this.input.attr('maxlength', this.properties.maxLength);
        }
    },

    renderReadOnly: function () {
        var template = this.renderFactory.getTemplate("text-readOnly");
        var value = this.getValue();
        value = (typeof value !== "undefined" && value !== null) ? bizagi.util.sanitizeHTML(value) : "";
        
        var data = {
            value: value,
            editable: this.properties.editable
        };

        return $.fasttmpl(template, data);
    },
    
    /**
     * Sets the value in the rendered control
     * @param {} value     
     */
    setDisplayValue: function (value) {
        var self = this;
        var decodedValue = bizagi.util.decodeURI(value);
        value = this.validateValue((typeof (decodedValue) == "string" ? decodedValue : ""), self.properties.maxLength);

        // Set internal value
        self.setValue(value, false);

        if (value) {
            value = $.br2nl(value).replaceAll('\\n', '\n') ;            
        }

       // Set value in input           
        if (self.properties.editable){                   
            self.input.val(value);
        } else {
            $(".bz-text-readonly", self.getControl()).html(bizagi.util.sanitizeHTML(value));
        }   
    },

    attachRetypeDouble: function () {
        var self = this;
        var element = self.element;

        var input = $("input", element);
        if (!input || input.length === 0) return;

        // Apply blur handler to check value
        input.blur(function () {
            var control = $(this);
            self.retypeDouble(control);
        });
    },

    retypeDouble: function (control) {
        var self = this;
        var currentValue = control.val();
        var isVisible = $(control).attr("type") !== "hidden" && $(control).css("display") !== "none" && $(control).css("visibility") !== "hidden";

        if (isVisible) {
            if (!control.data("oldValue") || $(this).data("oldValue") === "") {

                // Check that there is something in the value or if a value has already been set
                if (currentValue === "" || currentValue === control.attr("newValue")) return;

                self.createTooltip(control, currentValue, self.getResource("render-number-retype"))

            } else {
                // Check that there is something in the value
                if (currentValue === "") return;

                if (currentValue !== control.data("oldValue")) {
                    self.setValue("");

                    self.createTooltip(control, "", self.getResource("render-text-retype-error"))
                } else {

                    control.data("newValue", currentValue);
                    control.data("oldValue", "");

                    // Destroy tooltips
                    control.tooltip("destroy");
                }
            }
        }
    },

    getElementsToStylize: function ($context) {        
        return this.properties.editable ? $("input", $context) : $(".bz-text-readonly", $context); 
    },
    //#endregion Side effects funtions
      
});
